import Grid from '@material-ui/core/Grid';
import React from 'react';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';

const Welcome = () => {
    return (
    <Grid item xs={6}
        container
        direction="row"
        alignItems="center">
        <Card>
            <Typography
                variant="h6"
                component="h4"
                align="center"
                paragraph>
                Witaj na stronie do zarzdzania kartotekami medycznymi, moesz tu dokona przegldu swojej karty lub umwi wizyt u lekarza.
            </Typography>
        </Card>
    </Grid>
    )
};

export default Welcome;